/* ./src/index.css */
@tailwind base;
@tailwind components;
@layer base {
  @font-face {
    font-family: "Bebas";
    src: local("Bebas"), url(/src/fonts/BebasNeue-Regular.ttf);
  }
  @font-face {
    font-family: "Trebuchet";
